import { fetchCurrency as apiGetCurrency } from 'api/currency';
import { createAction } from 'typesafe-actions';
import { getReportId } from './currencyCommon';
export const fetchCurrencyRequest = createAction('currency/fetch/request')();
export const fetchCurrencySuccess = createAction('currency/fetch/success')();
export const fetchCurrencyFailure = createAction('currency/fetch/failure')();
export function fetchCurrency() {
    return dispatch => {
        const meta = {
            reportId: getReportId(),
        };
        dispatch(fetchCurrencyRequest(meta));
        return apiGetCurrency()
            .then(res => {
            dispatch(fetchCurrencySuccess(res.data, meta));
        })
            .catch(err => {
            dispatch(fetchCurrencyFailure(err, meta));
        });
    };
}
