import { getReportId, stateKey } from './costTypeCommon';
export const selectCostTypeState = (state) => state[stateKey];
// Fetch costType
export const selectCostType = (state) => selectCostTypeState(state).byId.get(getReportId());
export const selectCostTypeFetchStatus = (state) => selectCostTypeState(state).fetchStatus.get(getReportId());
export const selectCostTypeError = (state) => selectCostTypeState(state).errors.get(getReportId());
