import axios from 'axios';
import { isBetaFeature } from 'utils/feature';
import { getCostType } from 'utils/localStorage';
export const ReportTypePaths = {
    ["cost" /* cost */]: 'reports/aws/costs/',
    ["database" /* database */]: 'reports/aws/costs/',
    ["network" /* network */]: 'reports/aws/costs/',
    ["storage" /* storage */]: 'reports/aws/storage/',
    ["instance_type" /* instanceType */]: 'reports/aws/instance-types/',
};
export function runReport(reportType, query) {
    const path = ReportTypePaths[reportType];
    // Todo: Show in-progress features in beta environment only
    if (isBetaFeature()) {
        switch (reportType) {
            case "cost" /* cost */:
            case "database" /* database */:
            case "network" /* network */:
                return axios.get(`${path}?cost_type=${getCostType()}&${query}`);
        }
    }
    return axios.get(`${path}?${query}`);
}
