import { fetchCostType as apiGetCostType } from 'api/costType';
import { createAction } from 'typesafe-actions';
import { getReportId } from './costTypeCommon';
export const fetchCostTypeRequest = createAction('costType/fetch/request')();
export const fetchCostTypeSuccess = createAction('costType/fetch/success')();
export const fetchCostTypeFailure = createAction('costType/fetch/failure')();
export function fetchCostType() {
    return dispatch => {
        const meta = {
            reportId: getReportId(),
        };
        dispatch(fetchCostTypeRequest(meta));
        return apiGetCostType()
            .then(res => {
            dispatch(fetchCostTypeSuccess(res.data, meta));
        })
            .catch(err => {
            dispatch(fetchCostTypeFailure(err, meta));
        });
    };
}
