import { getReportId, stateKey } from './currencyCommon';
export const selectCurrencyState = (state) => state[stateKey];
// Fetch currency
export const selectCurrency = (state) => selectCurrencyState(state).byId.get(getReportId());
export const selectCurrencyFetchStatus = (state) => selectCurrencyState(state).fetchStatus.get(getReportId());
export const selectCurrencyError = (state) => selectCurrencyState(state).errors.get(getReportId());
